'use client'

import { useEffect, useCallback } from 'react'
import { Button } from '@/components/ui/button'
import { Alert, AlertDescription } from '@/components/ui/alert'
import { Badge } from '@/components/ui/badge'
import { Loader2, RotateCcw, Play, Clock } from 'lucide-react'
import { ScreenerFilter } from '@/components/screener/ScreenerFilter'
import { ScreenerResults } from '@/components/screener/ScreenerResults'
import { PresetList } from '@/components/screener/PresetList'
import { useScreenerStore } from '@/stores/screenerStore'
import { useScreenerPresets } from '@/stores/screenerPresetsStore'

function formatLastUpdated(dateString: string | null): string {
  if (!dateString) return 'Never'
  const date = new Date(dateString)
  return date.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit', second: '2-digit' })
}

export default function ScreenerPage() {
  const {
    runScreener,
    clearFilters,
    loading,
    error,
    filters,
    last_updated,
  } = useScreenerStore()

  const { fetchPresets } = useScreenerPresets()

  const handleRunScreener = useCallback(() => {
    runScreener()
  }, [runScreener])

  const handleClearFilters = useCallback(() => {
    clearFilters()
  }, [clearFilters])

  useEffect(() => {
    runScreener()
    fetchPresets()
  }, [])

  const activeFilterCount = Object.values(filters).filter(
    (v) => v !== undefined && v !== null && v !== ''
  ).length
  const isFiltered = activeFilterCount > 0

  return (
    <div className="container mx-auto py-6 space-y-6">
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-3xl font-bold tracking-tight">Stock Screener</h1>
          <p className="text-muted-foreground mt-1">
            Find stocks and assets that match your investment criteria
          </p>
          <div className="flex items-center gap-3 mt-2 text-sm">
            {isFiltered && (
              <Badge variant="secondary">
                {activeFilterCount} filter{activeFilterCount !== 1 ? 's' : ''} applied
              </Badge>
            )}
            <span className="text-muted-foreground flex items-center gap-1">
              <Clock className="h-3 w-3" />
              Last updated: {formatLastUpdated(last_updated)}
            </span>
          </div>
        </div>
        <div className="flex gap-2">
          <Button
            variant="outline"
            onClick={handleClearFilters}
            disabled={loading || !isFiltered}
          >
            <RotateCcw className="h-4 w-4 mr-2" />
            Clear All
          </Button>
          <Button
            onClick={handleRunScreener}
            disabled={loading}
          >
            {loading ? (
              <Loader2 className="h-4 w-4 mr-2 animate-spin" />
            ) : (
              <Play className="h-4 w-4 mr-2" />
            )}
            Run Screener
          </Button>
        </div>
      </div>

      {error && (
        <Alert variant="destructive">
          <AlertDescription>{error}</AlertDescription>
        </Alert>
      )}

      <div className="grid lg:grid-cols-4 gap-6 h-[calc(100vh-200px)]">
        <div className="lg:col-span-1 space-y-4">
          <PresetList />
          <ScreenerFilter />
        </div>
        <div className="lg:col-span-3">
          <ScreenerResults />
        </div>
      </div>
    </div>
  )
}
